/* open job post (animation) */
div#job-description-wrapper {
    position: absolute;
    overflow: hidden;
    width: 500px;
    height: 200px;
    right: -300px;
    bottom: 100px;
}
div#job-description > div {
    position: absolute;
    background-color: cornsilk;
    right: -500px;
    width: 500px;
    -webkit-animation: JobPostAnimation 1s;
            animation: JobPostAnimation 1s;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    z-index:2;
    word-wrap:break-word;
}
@-webkit-keyframes JobPostAnimation {
    100% {
        right: 0px;
    }
}
@keyframes JobPostAnimation {
    100% {
        right: 0px;
    }
}
.bgOpacity {
    -webkit-animation: bgOpacity 2s;
            animation: bgOpacity 2s;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}
@-webkit-keyframes bgOpacity {
    100% {
        opacity: 0.5;
    }
}
@keyframes bgOpacity {
    100% {
        opacity: 0.5;
    }
}

/* announcement styling (animation) */
.animated-text {
    background-color: rgb(63, 89, 135);
    color: white;
    -webkit-transform: translate(-50, -50%);
            transform: translate(-50, -50%);
    padding: 0 40px;
    height: 50px;
    overflow: hidden;
}
.line {
    text-transform: uppercase;
    text-align: center;
    font-size: 30px;
    line-height: 50px;
}
.line:first-child {
    -webkit-animation: slide 12s infinite;
            animation: slide 12s infinite;
}
@-webkit-keyframes slide {
    0% {
        margin-top: 0px;
    }
    2% {
        margin-top: 0px;
    }
    16% {
        margin-top: -50px;
    }
    18% {
        margin-top: -50px;
    }
    33% {
        margin-top: -100px;
    }
    35% {
        margin-top: -100px;
    }
    66% {
        margin-top: -150px;
    }
    82% {
        margin-top: -200px;
    }
    100% {
        margin-top: -250px;
    }
}
@keyframes slide {
    0% {
        margin-top: 0px;
    }
    2% {
        margin-top: 0px;
    }
    16% {
        margin-top: -50px;
    }
    18% {
        margin-top: -50px;
    }
    33% {
        margin-top: -100px;
    }
    35% {
        margin-top: -100px;
    }
    66% {
        margin-top: -150px;
    }
    82% {
        margin-top: -200px;
    }
    100% {
        margin-top: -250px;
    }
}
